import { useEffect, useState } from 'react';
import http from 'https://unpkg.com/isomorphic-git@beta/http/web/index.js';

export function InitGitPage({ state, updateState }) {
    const [text, setText] = useState('git clone ...');

    useEffect(() => {
        const fs = new LightningFS('fs')
        const dir = '/test-clone2'

        setText('git clone: ' + state.gitUrl);

        git.clone({
            fs, http, dir,
            url: state.gitUrl,
            //corsProxy: 'https://cors.isomorphic-git.org'
        })
            .then((text) => {
                console.log("---- finish " + text);
                // setText(text);
                fs.readdir(dir, null, (err, files) => {
                    console.log(files);
                    setText(JSON.stringify(files));
                });
            })
    }, [])

    return (
        <>
            <p>{text}</p>
        </>
    );
}
